import React from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Row, Col, Alert } from 'reactstrap'
import Reliefs from '../reliefs'


const Home = props =>
  (
    <div className='bg-white m-1 mt-3'>
      {
        props.errorIds.length > 0 &&
        <Alert color='danger' className='rounded position-sticky' style={{ 'top': '85px', 'zIndex': '10' }}>
          <h5 className='alert-heading'>Validation Errors</h5>
          <hr />
          <p className='m-0'>
            <span>Please re-enter amount for row# </span>
            {
              props.errorIds.map((id, index) => <span><span className='font-weight-bold'>{id}</span>
                <span>{index < props.errorIds.length - 1 && ', '}</span></span>)
            }
          </p>
        </Alert>

      }
      <div style={{ 'display': 'flex', 'flexDirection': 'column', 'flexWrap': 'nowrap' }}>

        <Row noGutters className='rounded border bg-secondary' >
          <Col xs='1'>
            <p className='p-1  font-weight-bold text-left'>#</p>
          </Col>
          <Col xs='6' sm='7'>
            <p className='p-1  font-weight-bold text-left'>Relief Types</p>
          </Col>
          <Col xs='2'>
            <p className='p-1  font-weight-bold text-left'>Limit (RM)</p>
          </Col>
          <Col xs='3' sm='2'>
            <p className='p-1  font-weight-bold text-left'>Relief Amount (RM)</p>
          </Col>
        </Row>
        <Reliefs />
      </div>
    </div>

  )





const mapStateToProps = state => ({
  errorIds: state.relief.errorIds
})


const mapDispatchToProps = dispatch => bindActionCreators({
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home)



